/* ---------------------------------------
Homepage SVG Logo */
.homepage-circles {
  -webkit-transform-origin: 160px 20px 0;
          transform-origin: 160px 20px 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.core-homepage-svg:hover .homepage-circles {
  -webkit-animation: rotate 1.5s 1;
          animation: rotate 1.5s 1;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate3d(1, -1, 0, 0deg);
            transform: rotate3d(1, -1, 0, 0deg);
  }
  100% {
    -webkit-transform: rotate3d(1, -1, 0, 360deg);
            transform: rotate3d(1, -1, 0, 360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate3d(1, -1, 0, 0deg);
            transform: rotate3d(1, -1, 0, 0deg);
  }
  100% {
    -webkit-transform: rotate3d(1, -1, 0, 360deg);
            transform: rotate3d(1, -1, 0, 360deg);
  }
}
/* ---------------------------------------
Homepage Header */
.core-header {
  background-color: #0078aa;
  transition: background-color 0.6s ease;
}
.core-header p,
.core-header .core-homepage-svg {
  transition: opacity 0.6s ease, -webkit-transform 0.6s ease;
  transition: transform 0.6s ease, opacity 0.6s ease;
  transition: transform 0.6s ease, opacity 0.6s ease, -webkit-transform 0.6s ease;
}
.core-header.scrolled {
  background-color: #014e70;
}
.core-header.scrolled p,
.core-header.scrolled .core-homepage-svg {
  opacity: 0.3;
  -webkit-transform: translateY(-20%);
          transform: translateY(-20%);
}

