/*----------------------------------------
Grid Highlighting:
Apply background colors to easily view the columns for the styleguide only
*/
.sg-grid-example {
  background: #ececec;
  margin-bottom: 1em;
  padding-top: 1.5em;
}

.sg-grid-example [class^=col-] p,
.sg-grid-example [class*=col-] p {
