  padding: 1em 0;
  text-align: center;
}

.sg-grid-example [class^=col-] div p,
.sg-grid-example [class*=col-] div p {
  background: #d8d8d8;
}

.sg-grid-highlight {
  color: #444;
  font-weight: bold;
}

.sg-grid-overlay {
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 20000;
}

.sg-grid-overlay div,
.sg-grid-overlay .row,
.sg-grid-overlay [class^=col-],
.sg-grid-overlay [class^=col-] div {
  height: 100%;
}

.sg-grid-overlay [class^=col-] div {
  background-color: rgba(4, 150, 255, 0.12);
}

